.products {
  padding-top: 121px;
  padding-bottom: 98px;

  background-color: white;

  text-align: center;
}

.products__label {
  margin-bottom: 15px;
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: 21px;

  text-transform: uppercase;

  color: #FFA5BA;

}

.products__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: 39px;

  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #D41443;

  margin-bottom: 140px;

}

.products-list__item {
  position: relative;

  background-color: #FFB8CA;
  border-radius: 24px;

  padding: 160px 40px 40px 40px;
}

.products-list__item:not(:last-child) {
  margin-bottom: 130px;
}

.products-list__title::before {
  position: absolute;
  top: 0%;
  left: 50%;
  transform: translate(-50%, -50%);

  content: '';
  display: block;
  width: 249px;
  height: 252px;

  background-image: url(../images/products/icecream-1.png);
  background-size: contain;
}

.products-list__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 23px;

  letter-spacing: 0.04em;
  text-transform: uppercase;

  color: #FFFFFF;
}

.products-list__title::after {
  content: '';
  display: block;
  width: 26px;
  height: 6px;

  background-image: url(../images/products/dots.png);
  background-size: contain;
  
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  margin-bottom: 30px;
}

.products-list__description {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 152%;

  letter-spacing: 0.04em;

  color: #FFFFFF;

  margin-bottom: 30px;
}

.products-list__button {
  width: 40px;
  height: 40px;
 
  background-image: url(../images/products/arrow-right.png);
  background-size: contain;

  border: transparent;
  border-radius: 50%;

  cursor: pointer;

   padding: 0;
}

$colors: "#F44336",
"#FFB8CA",
"#F0D1A5",
"#C2E297";

@include colorize($colors, ".products-list__item");